@import '../../utility/theme/variable.scss';

.loader {
  width: 100%;
  height: 100vh;
  background-color: $primaryColor;
  display: flex;

  span {
    font-size: 2rem;
    color: $colorCyan;
    font-weight: bold;
    margin: auto;
  }
}

.error-screen {
  width: 100%;
  height: 100vh;
  background-color: var(--loaderBackground);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  // position: relative;
  img {
    width: 200px;
    height: 200px;
    // margin: auto;
  }
}

.beta-item {
  padding-bottom: 20px;
}

.card-container {
  display: flex;

  .app-bar {
    padding-right: 0 !important;

    .toolbar {
      display: flex;

      justify-content: space-between;
      align-items: center;
      height: 100%;
      margin: 0;

      &.toolbar-padding {
        padding: 0;
      }

      .menu-container {
        width: 280px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0;
        //border: red solid 2px;
        margin-left: $size20;
        height: 100%;

        .beta-tag img {
          height: 20px;
          width: 22px;
          margin-left: -5px;
          margin-bottom: -18px;
        }

        .toggle-drawer-icon {
          color: var(--drawerIconColour);
          font-size: 28px;
          padding: $size7;
          background: var(--drawerToggleIconBackground);
          height: 24px;
          width: 24px;
          margin-left: 65px;
          margin-bottom: 14px;

          border-radius: $size5;
          cursor: pointer;

          &:hover {
            box-shadow: $colorCyan $size0 $size7 29px $size0;
          }
        }
      }

      .social-icons {
        position: absolute;
        left: 350px;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-around;
        box-sizing: border-box;
        padding: $size7;

        .icon {
          width: 33px;
          height: 33px;
          border-radius: 6px;
          margin-right: 10px;
          padding: 2px;
          background-color: #fff;
        }
      }

      .graph-heading {
        font-family: $primaryFontFamily;
        font-weight: $fontWeight400;
      }

      .chip {
        background-color: aliceblue;
      }

      .theme-icon {
        // border: var(--themeIconBorder) solid 0.5px;
        padding: $size7;
        border-radius: 5px;
        display: flex;
        align-items: center;
        margin-right: $size20;
        background-color: var(--iconBackgroundColor);
        cursor: pointer;

        svg {
          color: var(--iconColour);
          height: 24px;
        }
      }
    }
  }

  .drawer-first {
    &.MuiDrawer-paper {
      box-sizing: border-box;
      width: 300px;
      padding-bottom: 8rem;
      color: $colorWhite;
    }

    .list-drawer {
      width: 100%;
      max-width: 360;
      background-color: 'background.paper';
    }
  }

  .drawer-two {
    width: 300px;
    flex-shrink: 0;
  }
}

@media screen and (max-width: 600px) {
  .toolbar {
    .menu-container {
      width: 60px;
    }

    .left-header-part {
      width: calc(100% - 60px);
    }
  }
}
